import React from 'react';
import { useEffect, useRef, useState } from "react";
import 'aframe';
import 'aframe-video-controls';
import ChosenHighlight from "./ChosenHighlight";



const VRThreeJS = () => {
  const [team, setTeam] = useState();
  const videoURL = process.env.PUBLIC_URL + '/' +'360-sea-mountain.mp4';
  
  useEffect(() => {
    
    console.log(team);
  }, [team]);


  return (
    <a-scene>
      <a-assets>
        <video id="video_1" src={videoURL} autoPlay controls></video>
        {/* <ChosenHighlight team={team} /> */}
      </a-assets>

      <a-camera position="0 0 5">
        <a-cursor id="cursor" color="yellow"></a-cursor>
      </a-camera>

      <a-videosphere src="#video_1" rotation="0 180 0"></a-videosphere>

      {/* <a-entity video-controls="src:#video_1"></a-entity> */}
    </a-scene>
  );
};

export default VRThreeJS;
